import Model from "flarum/Model";

export default class DecorationStorePurchase extends Model {}
Object.assign(DecorationStorePurchase.prototype, {
  id: Model.attribute("id"),
  item_id: Model.attribute("item_id"),
  user_id: Model.attribute("user_id"),
  item_count: Model.attribute("item_count"),
  item_type: Model.attribute("item_type"),
  purchase_type: Model.attribute("purchase_type"),
  purchase_cost: Model.attribute("purchase_cost"),
  purchase_discount: Model.attribute("purchase_discount"),
  assigned_at: Model.attribute("assigned_at"),
  item_status: Model.attribute("item_status"),
  expired_days: Model.attribute("expired_days"),
  is_expired: Model.attribute("is_expired"),
  decorationData: Model.hasOne("decorationData"),
});
