import UserPage from "flarum/components/UserPage";
import DecorationStorePurchaseListPage from "./DecorationStorePurchaseListPage";

export default class DecorationStorePurchaseIndexPage extends UserPage {
  oninit(vnode) {
    super.oninit(vnode);
    this.loadUser(m.route.param("username"));
  }

  content() {
    if(app.session.user){
      const currentUserID = app.session.user.id();
      const targetUserID = this.user.id();

      if(currentUserID===targetUserID){
        return (
          <div className="DecorationStoreEquipmentPage">
            {DecorationStorePurchaseListPage.component({
              params: {
                user: this.user,
              },
            })}
          </div>
        );
      }else{
        m.route.set(app.route("decorationStore"));
      }
    }else{
        m.route.set(app.route("decorationStore"));
    }
  }
}
