import { extend } from "flarum/extend";
import UserPage from "flarum/components/UserPage";
import LinkButton from "flarum/components/LinkButton";
import DecorationStoreEquipmentPage from './components/DecorationStoreEquipmentPage';
import DecorationStorePurchasePage from './components/DecorationStorePurchasePage';

export default function () {
  app.routes["user.decorationStoreEquipment"] = {
    path: "/u/:username/decorationStoreEquipment",
    component: DecorationStoreEquipmentPage,
  };

  app.routes["user.decorationStorePurchase"] = {
    path: "/u/:username/decorationStorePurchase",
    component: DecorationStorePurchasePage,
  };

  extend(UserPage.prototype, "navItems", function (items,user) {
      if(app.session.user){
        const currentUserID = app.session.user.id();
        const targetUserID = this.user.id();

        if(currentUserID==targetUserID){
          items.add(
            "decorationStorePurchase",
            LinkButton.component({
                href: app.route("user.decorationStorePurchase", {
                  username: this.user.username(),
                }),
                icon: "fas fa-store",
              },
              [
                app.translator.trans(
                  "wusong8899-decoration-store.forum.purchase-history"
                )
              ]
            ),
            10
          );
          items.add(
            "decorationStoreEquipment",
            LinkButton.component({
                href: app.route("user.decorationStoreEquipment", {
                  username: this.user.username(),
                }),
                icon: "fas fa-store",
              },
              [
                app.translator.trans(
                  "wusong8899-decoration-store.forum.my-decoration"
                )
              ]
            ),
            10
          );
        }
      }
  });
}
