import Page from 'flarum/components/Page';
import IndexPage from 'flarum/components/IndexPage';
import listItems from 'flarum/common/helpers/listItems';
import LoadingIndicator from "flarum/components/LoadingIndicator";
import Button from 'flarum/components/Button';
import Select from 'flarum/common/components/Select';
import Stream from 'flarum/utils/Stream';

import DecorationStoreListItem from "./DecorationStoreListItem";
import navigationOptionDropdown from "../common/util/navigationOptionDropdown";

export default class DecorationStoreIndexPage extends Page {
  oninit(vnode) {
    super.oninit(vnode);
    this.bodyClass = 'App--index';
    this.loading = true;
    this.moreResults = false;
    this.decorationStoreList = [];

    this.filterItemType = Stream("-1");
    this.filterItemPurchaseType = Stream("-1");
    this.filterActivationStatus = Stream(1);

    const itemTypeList = app.forum.attribute("decorationStoreItemTypes");
    this.itemTypeOption = {};
    this.itemTypeOption['-1'] = app.translator.trans('wusong8899-decoration-store.lib.item-type-all');

    for(let index in itemTypeList){
      const itemTypeValue = itemTypeList[index];
      this.itemTypeOption[itemTypeValue] = app.translator.trans('wusong8899-decoration-store.lib.item-type-'+itemTypeValue);
    }

    this.loadResults();

  }

  view() {
    let loading;
    let itemCount = 0;

    if (this.loading) {
      loading = LoadingIndicator.component({ size: "large" });
    }

    return (
      <div className="IndexPage">
        {IndexPage.prototype.hero()}

        <div className="container">
          <div className="sideNavContainer">
            <nav className="IndexPage-nav sideNav">
              <ul>{listItems(IndexPage.prototype.sidebarItems().toArray())}</ul>
            </nav>
            <div className="DecorationStoreIndexContainer">
              <div>
                {navigationOptionDropdown("item0")}
              </div>
              <div className="DecorationStoreIndexButtonsContainer">
                <div className="DecorationStoreFiltersFlexCard">
                  <div className="DecorationStoreFiltersCardsListItem DecorationStoreFiltersCard">
                    <Select
                      value={this.filterItemType()}
                      className="DecorationStoreSelectionList" 
                      disabled={this.loading}
                      options={this.itemTypeOption}
                      buttonClassName="Button"
                      onchange={(e) => {
                        this.filterItemType(e);
                        this.filterItem(e)
                      }}
                    />
                  </div>
                  <div className="DecorationStoreFiltersCardsListItem DecorationStoreFiltersCard">
                    <Select
                      value={this.filterItemPurchaseType()}
                      className="DecorationStoreSelectionList" 
                      disabled={this.loading}
                      options={{
                        '-1': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-all'),
                        'onetime': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-onetime'),
                        'monthly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-monthly'),
                        'yearly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-yearly'),
                      }}
                      buttonClassName="Button"
                      onchange={(e) => {
                        this.filterItemPurchaseType(e);
                        this.filterItem(e)
                      }}
                    />
                  </div>
                </div>
              </div>

              <div id="decorationStoreItemsContainer" class="DecorationStoreGlobalContainer DecorationItemFlexCard">
                {this.decorationStoreList.map((itemData) => {
                  let itemClass = "DecorationItemCardsListItem DecorationItemCard";

                  if(itemCount%3!==0){
                    itemClass+=" DecorationItemCardAdjust";
                  }

                  itemCount++;

                  return (
                    <div class={itemClass}>
                      {DecorationStoreListItem.component({ itemData})}
                    </div>
                  );
                })}
              </div>

              {!this.loading && this.decorationStoreList.length===0 && (
                <div>
                  <div style="font-size:1.4em;color: var(--muted-more-color);text-align: center;height: 300px;line-height: 100px;">{app.translator.trans("wusong8899-decoration-store.lib.list-empty")}</div>
                </div>
              )}

              {!loading && this.hasMoreResults() && (
                <div style="text-align:center;padding:20px">
                  <Button className={'Button Button--primary'} disabled={this.loading} loading={this.loading} onclick={() => this.loadMore()}>
                    {app.translator.trans('wusong8899-decoration-store.lib.list-load-more')}
                  </Button>
                </div>
              )}

              {loading && <div className="DecorationStore-loadMore">{loading}</div>}
            </div>

          </div>
        </div>
      </div>
    );
  }

  loadMore() {
    this.loading = true;
    this.loadResults(this.decorationStoreList.length);
  }

  filterItem(e){
    $("#decorationStoreItemsContainer").empty();
    this.decorationStoreList = [];
    this.loading = true;
    m.redraw();
    this.loadResults();
  }

  parseResults(results) {
    this.moreResults = !!results.payload.links && !!results.payload.links.next;
    [].push.apply(this.decorationStoreList, results);
    this.loading = false;
    m.redraw();

    return results;
  }

  hasMoreResults() {
    return this.moreResults;
  }

  loadResults(offset = 0) {
    const filters = {
      item_type:this.filterItemType(),
      purchase_type:this.filterItemPurchaseType(),
      isActivate:this.filterActivationStatus(),
    };

    return app.store
      .find("decorationStoreList", {
        filter:filters,
        page: {
          offset,
        },
      })
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }

}
