import app from 'flarum/forum/app';
import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';

export default class DecorationStoreSoldOutModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;
  
  oninit(vnode) {
    super.oninit(vnode);
    this.itemData = this.attrs.itemData;
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return this.itemData.item_title();
  }

  content() {
    return (
      <div className="Modal-body">
        <div className="Form">
          <div className="Form-group">
            <div style="text-align: center;font-size: 18px;">{app.translator.trans('wusong8899-decoration-store.forum.item-soldout')}</div>
          </div>
          <div className="Form-group" style="text-align: center;">
            {Button.component(
              {
                className: 'Button Button--primary',
                loading: this.loading,
                onclick: () => {
                  this.hide();
                },
              },
              app.translator.trans('wusong8899-decoration-store.lib.ok')
            )}
          </div>
        </div>
      </div>
    );
  }
}
