import app from 'flarum/forum/app';
import Modal from 'flarum/common/components/Modal';
import Button from 'flarum/common/components/Button';

export default class DecorationStorePurchaseOwnedModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;

  oninit(vnode) {
    super.oninit(vnode);
    this.itemData = this.attrs.itemData;
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return this.itemData.item_title();
  }

  content() {
    return [
      <div className="Modal-body">
        <div className="DecorationStoreConfirmationText">
            <div>{app.translator.trans('wusong8899-decoration-store.forum.item-already-have')}</div>
        </div>
        <div style="text-align:center">
            {Button.component({
                style:'width:66px',
                className: 'Button Button--primary',
                onclick: () => {
                  this.hide();
                }
              },
              app.translator.trans('wusong8899-decoration-store.lib.ok')
            )}
          </div>
      </div>,
    ];
  }
}