import app from 'flarum/forum/app';
import Modal from 'flarum/common/components/Modal';
import Button from 'flarum/common/components/Button';

export default class DecorationStorePurchaseSuccessModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;

  oninit(vnode) {
    super.oninit(vnode);
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return app.translator.trans('wusong8899-decoration-store.forum.purchase-success');
  }

  content() {
    return [
      <div className="Modal-body">
        <div style="text-align:center">
            {Button.component({
                style:'width:66px',
                className: 'Button Button--primary',
                onclick: () => {
                  this.hide();
                }
              },
              app.translator.trans('wusong8899-decoration-store.lib.ok')
            )}
          </div>
      </div>,
    ];
  }
}