import { extend } from 'flarum/extend';
import app from 'flarum/forum/app';
import IndexPage from 'flarum/components/IndexPage';
import LinkButton from 'flarum/components/LinkButton';

export default function addSidebarMenu() {
  extend(IndexPage.prototype, 'navItems', function (items) {
    let decorationStoreDisplayNameSetting = app.forum.attribute("decorationStoreDisplayName");
    let decorationStoreDisplayName = app.translator.trans(decorationStoreDisplayNameSetting===""?'wusong8899-decoration-store.forum.display-name-default':decorationStoreDisplayNameSetting);
    
    items.add(
      'DecorationStore',
      <LinkButton icon="fas fa-store" href={app.route('decorationStore')}>
        {decorationStoreDisplayName}
      </LinkButton>,
      15
    );

    return items;
  });
}
